lookMyBaby: false,
<!--  -->
<template>
    <div class="sign-wrapper">
        <div class="sign-main sign-main-forget">
            <!-- 左侧form -->
            <div class="sign-main_left">
                <h1>{{$t('sign.forget[0]')}}</h1>
                <h4>{{$t('sign.register[1]')}}</h4>
                <el-form :model="ruleForm" status-icon :rules="rules" label-position='top' ref="ruleForm"
                    label-width="80px" class="form-forget form">
                    <el-form-item :label="$t('sign.register[2]')" prop="mobile">
                        <el-input :placeholder="$t('sign.register[3]')" v-model="ruleForm.mobile" autocomplete="off">
                        </el-input>
                    </el-form-item>
                    <el-form-item :label="$t('sign.register[4]')" prop="code">
                        <div class="verifyCode">
                            <el-input :placeholder="$t('sign.register[5]')" v-model.number="ruleForm.code"
                                autocomplete="off">
                            </el-input>
                            <el-button :loading="codeLoading" :disabled="btnCode.disabled" @click="getCode">
                                {{btnCode.time}}
                            </el-button>
                        </div>
                    </el-form-item>
                    <!-- 登录密码 -->
                    <el-form-item :label="$t('sign.register[6]')" prop="opwd">
                        <div style="position: relative">
                            <!-- 请输入您的登录密码 -->
                            <el-input :placeholder="$t('sign.register[7]')" :type="lookMyBaby?'text' :'password'"
                                v-model="ruleForm.opwd" autocomplete="off"></el-input>
                            <!-- 小眼睛 -->
                            <img class="lookMyBaby" @click="lookMyBaby = !lookMyBaby" v-show="!lookMyBaby"
                                src="../../assets/img/sign/login_icon_7.png" alt="">
                            <img class="lookMyBaby" @click="lookMyBaby = !lookMyBaby" v-show="lookMyBaby"
                                src="../../assets/img/sign/login_icon_9.png" alt="">
                        </div>
                    </el-form-item>
                    <!-- 确认密码 -->
                    <el-form-item :label="$t('sign.register[8]')" prop="opwd1">
                        <div style="position: relative">
                            <el-input :placeholder="$t('sign.register[9]')" :type="lookMyBaby?'text' :'password'"
                                v-model="ruleForm.opwd1" autocomplete="off"></el-input>
                            <!-- 小眼睛 -->
                            <img class="lookMyBaby" @click="lookMyBaby = !lookMyBaby" v-show="!lookMyBaby"
                                src="../../assets/img/sign/login_icon_7.png" alt="">
                            <img class="lookMyBaby" @click="lookMyBaby = !lookMyBaby" v-show="lookMyBaby"
                                src="../../assets/img/sign/login_icon_9.png" alt="">
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" :loading="loading" class="btn-submit forget"
                            @click="submitForm('ruleForm')">{{$t('sign.register[19]')}}
                        </el-button>
                        <!-- 未有账号？ -->
                        <span class="guide">{{$t('sign.forget[1]')}}<a
                                @click="$router.push({path:'/register'})">{{$t('sign.forget[2]')}}</a></span>
                    </el-form-item>

                </el-form>
            </div>
            <!-- 分割线 -->
            <div class="river"></div>
            <!-- 右侧图片 -->
            <div class="sign-main_right sign-main_right-register">
                <img src="../../assets/img/sign/login_img_2.png" alt="">
                <p>
                    {{$t('sign.register[17]')}}
                </p>
            </div>



        </div>


    </div>
</template>

<script>
    import { reset_opwd, sendEmailMsg, sendMsg } from "@/api/public"
    export default {
        data() {
            var vAccount = (rule, value, callback) => {
                // 请输入正确的手机号/邮箱地址
                if (!value) {
                    callback(new Error(this.$t('sign.tips[0]')));
                    this.btnCode.disabled = true
                }
                if (!this.$public.email(value) && !this.$public.checkMobile(value)) {
                    callback(new Error(this.$t('sign.tips[1]')));
                    this.btnCode.disabled = true
                } else {
                    this.btnCode.disabled = false
                    callback();
                }
            };
            var vPwd = (rule, value, callback) => {
                if (!this.$public.pwd(value)) {
                    callback(new Error(this.$t('sign.tips[2]')));
                } else {
                    callback();
                }
            };
            var vPwd1 = (rule, value, callback) => {
                if (!this.$public.pwd(value)) {
                    callback(new Error(this.$t('sign.tips[2]')));
                }
                if (this.ruleForm.opwd != value) {
                    callback(new Error(this.$t('sign.tips[3]')));
                } else {
                    callback();
                }

            };
            return {
                lookMyBaby: false,
                isChecked: true,
                codeLoading: false,
                btnCode: {
                    time: this.$t('sign.tips[4]'),
                    disabled: false
                },
                loading: false,
                setTimer: "",
                ruleForm: {
                    mobile: '',
                    opwd: '',
                    opwd1: '',
                    code: '',
                },
                // 验证规则
                rules: {
                    mobile: [{ validator: vAccount, trigger: 'blur' }],
                    code: [{ required: true, trigger: 'blur', message: this.$t('sign.tips[5]') }],
                    opwd: [{ validator: vPwd, trigger: 'blur' }, { required: true, trigger: 'blur', message: this.$t('sign.tips[7]') }],
                    opwd1: [{ validator: vPwd1, trigger: 'blur' }, { required: true, trigger: 'blur', message: this.$t('sign.tips[7]') }],
                }
            };
        },

        components: {
        },

        computed: {},

        methods: {
            submitForm(form) {
                this.$public.throttle(() => {
                    this.$refs[form].validate((v) => {
                        if (v) {
                            this.loading = true
                            var sendData = JSON.parse(JSON.stringify(this.ruleForm))
                            sendData.opwd = this.$public.CryptographicLand(sendData.opwd)
                            sendData.opwd1 = this.$public.CryptographicLand(sendData.opwd1)
                            reset_opwd(sendData).then((r) => {
                                this.loading = false
                                if (r.data.status == 200) {
                                    this.$public.msg(this.$t('sign.tips[13]'), 'success', this)
                                    this.$router.push({ path: '/login' })
                                } else {
                                    this.$public.msg(r.data.msg, 'error', this)
                                }
                            }).catch((r) => { this.loading = false })
                        }
                    })
                }, 2000)

            },
            // 获取验证码
            getCode() {
                if (!this.ruleForm.mobile) {
                    this.$public.msg(this.$t('sign.tips[10]'), 'warning', this)
                    return
                }
                this.codeLoading = true
                if (this.ruleForm.mobile.indexOf("@") > -1) {
                    // 邮箱
                    sendEmailMsg({ email: this.ruleForm.mobile, type: 2 }).then((r) => {
                        this.codeLoading = false
                        if (r.data.status == 200) {
                            this.successMsg()
                        } else {
                            this.$public.msg(r.data.msg, 'error', this)
                        }
                    })
                } else {
                    // 手机
                    sendMsg({ mcode: this.ruleForm.mcode, mobile: this.ruleForm.mobile, type: 2 }).then((r) => {
                        this.codeLoading = false
                        if (r.data.status == 200) {
                            this.successMsg()
                        } else {
                            this.$public.msg(r.data.msg, 'error', this)
                        }
                    }).catch((r) => { this.codeLoading = false })
                }
            },
            // 发送成功
            successMsg() {
                this.btnCode.time = 59
                this.btnCode.disabled = true
                this.$message({
                    message: this.$t('sign.tips[11]'),
                    type: 'success'
                })
                this.setTimer = setInterval(() => {
                    this.btnCode.time--;
                    if (this.btnCode.time <= 0) {
                        this.btnCode = {
                            time: this.$t('sign.tips[12]'),
                            disabled: false
                        }
                        clearInterval(this.setTimer);
                    }
                }, 1000)
            },

        }
    }

</script>
<style lang=less>
    @import './sign.less';
    @import '../../assets/index.less';
</style>


